    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

::selection {
    background-color: #21524e;
}

body {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: whitesmoke;
    height: 100vh;
}

a {
    color: whitesmoke;
    text-decoration: none;
    cursor: pointer;
}

li {
    list-style-type: none;
}

h1 {
    font-size: 1.9rem;
}

header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 130px;
}

header .logo-container {
    display: flex;
    align-items: center;
    gap: .8rem;
}

.logo-container i {
    font-size: 1.5rem;
}

header .navbar ul {
    display: flex;
}

header .navbar li {
    font-size: 1.5rem;
}

header .navbar a {
    display: inline-block;
    padding: 1.5rem;
}

.navbar a::after {
    content: '';
    display: block;
    width: 0;
    height: 3px;
    background: #317b76;
    transition: width .3s;
}

.navbar a:hover::after {
    width: 100%;
}

.main-content {
    padding: 2rem;
}

.hero {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    animation: fadeIn 500ms ease-in;
}

.hero .left-container {
    display: flex; 
    flex-direction: column;
    width: 500px;
    gap: .5rem;
    align-items: center;
    padding: 1.3rem;
}

.left-container {
    font-size: 1.2rem;
}

.left-container h1 {
    padding: 0.5rem;
    font-size: 2.5rem;
}

.left-container span {
    margin-left: 1rem;
}

.left-container button {
    padding: .7rem;
    border: none;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    background-color: #317b76;
    color: inherit;
    border-radius: 5px;
}

.left-container button:hover {
    background-color: #21524e;
    cursor: pointer;
}

.right-container {
    padding: 1.3rem;
    position: relative;
}

.right-container div {
    width: 300px;
    height: 460px;
    border: 3px solid #317b76;
    position: absolute;
    transform: rotate(350deg);
}

.right-container img {
    width: 300px;
    transform: rotate(8deg);
    object-fit: cover;
}

.bottom-container {
    width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
    align-items: center;
    margin-top: 2.5rem;
    animation: fadeIn 500ms ease-in;
}

.bottom-container .img-container {
    width: 100px;
    height: 100px;
    text-align: center;
}

.bottom-container .img-container img {
    width: 100%;
    height: 100%;
    border-radius: 50px;
    border: 2px solid #21524e;
    object-fit: cover;
    cursor: pointer;
    transition: transform 200ms ease-in;
}

.bottom-container .img-container img:hover {
    transform: scale(1.1);
}

.img-container a {
    font-size: 1.2rem;
    color: whitesmoke;
    font-weight: bold;
}

/* Menu styling */

.menu-container {
    display: grid;
    text-align: center;
    grid-template-columns: repeat(2, 500px);
    place-content: center center;
    place-items: center center;
    gap: 1.5rem;
    animation: fadeIn 500ms ease-in;
}

.menu-container .menu-header {
    grid-column: 1/3;
    display: flex;
    align-items: center;
    padding: 1rem;
}
.menu-header h1 {
    font-size: 3rem;
    border-bottom: 3px solid #21524e;
}

.menu-container a {
    background-color: rgba(245,245,245, 0.7);
    border: 2px solid #21524e;
    color: #317b76;
    width: 400px;
    padding: 1rem;
    transition: transform 200ms ease-in;
    transition: background-color 500ms linear;
}

.menu-container img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border: 1px solid #317b76;
} 

.menu-container a:hover {
    transform: scale(1.05);
    background-color: rgba(33, 82, 78, 0.9);
    color: whitesmoke;
}

.menu-container .text-container {
    margin-top: 1rem;
}

.menu-container p {
    font-size: 1.3rem;
    font-weight: bold;
}

/* About section styling */

.about-container {
    display: flex;
    width: 1000px;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(33, 82, 78, 0.7);
    padding: 2rem;
    animation: fadeIn 500ms ease-in;
}

.about-header {
    text-align: center;
    width: 700px;
    padding: 1rem;
}

.about-header h1 {
    font-size: 2.1rem;
}

.about-header p {
    text-align: left;
    padding: 1rem;
}

.about-body {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.about-body h1 {
    font-size: 2.1rem;
    padding: 1.5rem;
}


.about-body .contact {
    display: grid;
    gap: 1rem;
    grid-template-columns: 300px 600px;
}

.contact .contact-info {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    color: inherit;
    text-align: left;
    padding: 1rem;
}

.form-container p {
    text-align: left;
    padding: 1rem;
}

.contact #form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.7rem;
}

#form input {
    width: 100%;
    height: 2rem;
    border: none;
    background-color: transparent;
    border-bottom: 2px solid whitesmoke;
    font-size: 1.1rem;
    padding: 1rem;
    color: whitesmoke;
}

#form textarea {
    width: 100%;
    height: 6rem;
    padding: .5rem;
}

#form button {
    width: 100%;
    padding: .6rem;
    font-size: 1.2rem;
    font-weight: bold;
    border: none;
    cursor: pointer;
    background-color: whitesmoke;
    color: #21524e;
}

#form input:focus,
#form textarea:focus,
#form button:focus {
    outline: 1px solid #21524e;
}

#form button:hover {
    background-color: #21524e;
    color: whitesmoke;
}

.contact .map {
    grid-column: 2/3;
    grid-row: 1/3;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,UAAU;IACV,SAAS;AACb;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yDAA4C;IAC5C,4BAA4B;IAC5B,sBAAsB;IACtB,4BAA4B;IAC5B,iBAAiB;IACjB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,UAAU;AACd;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,cAAc;IACd,QAAQ;IACR,WAAW;IACX,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,UAAU;IACV,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,cAAc;IACd,YAAY;IACZ,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;IAChB,yBAAyB;IACzB,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,cAAc;IACd,aAAa;IACb,6BAA6B;IAC7B,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,+BAA+B;AACnC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,yBAAyB;IACzB,iBAAiB;IACjB,eAAe;IACf,mCAAmC;AACvC;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA,iBAAiB;;AAEjB;IACI,aAAa;IACb,kBAAkB;IAClB,uCAAuC;IACvC,4BAA4B;IAC5B,0BAA0B;IAC1B,WAAW;IACX,+BAA+B;AACnC;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,mBAAmB;IACnB,aAAa;AACjB;AACA;IACI,eAAe;IACf,gCAAgC;AACpC;;AAEA;IACI,wCAAwC;IACxC,yBAAyB;IACzB,cAAc;IACd,YAAY;IACZ,aAAa;IACb,mCAAmC;IACnC,yCAAyC;AAC7C;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,sBAAsB;IACtB,uCAAuC;IACvC,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA,0BAA0B;;AAE1B;IACI,aAAa;IACb,aAAa;IACb,cAAc;